.logo {
  max-width: 142px;
  width: 100%;
  max-height: 33px;
  object-fit: cover;
}
@media screen and (max-width: 790px) {
    .logo {
        margin-left: 27px;
      }
}
@media screen and (max-width: 530px) {
    .logo {
        max-width: 103px;
        width: 100%;
        max-height: 24px;
      }
}
